const generateAdminMockOrders = (products) => {
  // Enhanced customer data
  const customers = [
    { id: 1, name: "John Doe", email: "john.doe@email.com", phone: "+1-555-0123" },
    { id: 2, name: "Jane Smith", email: "jane.smith@email.com", phone: "+1-555-0124" },
    { id: 3, name: "Robert Johnson", email: "robert.j@email.com", phone: "+1-555-0125" },
    { id: 4, name: "Emily Davis", email: "emily.davis@email.com", phone: "+1-555-0126" },
    { id: 5, name: "Michael Brown", email: "m.brown@email.com", phone: "+1-555-0127" },
    { id: 6, name: "Sarah Wilson", email: "sarah.w@email.com", phone: "+1-555-0128" },
    { id: 7, name: "David Miller", email: "david.miller@email.com", phone: "+1-555-0129" },
    { id: 8, name: "Lisa Anderson", email: "lisa.anderson@email.com", phone: "+1-555-0130" },
    { id: 9, name: "James Taylor", email: "james.taylor@email.com", phone: "+1-555-0131" },
    { id: 10, name: "Maria Garcia", email: "maria.garcia@email.com", phone: "+1-555-0132" }
  ];
  
  // Generate random dates within the last 60 days
  const getRandomDate = (daysAgo = 60) => {
    const now = new Date();
    const randomDays = Math.floor(Math.random() * daysAgo);
    now.setDate(now.getDate() - randomDays);
    // Add random hours and minutes
    now.setHours(Math.floor(Math.random() * 24));
    now.setMinutes(Math.floor(Math.random() * 60));
    return now.toISOString();
  };
  
  // Generate order statuses
  const statuses = ['pending', 'processing', 'shipped', 'delivered', 'cancelled'];
  const getRandomStatus = () => {
    const weights = [0.1, 0.2, 0.3, 0.35, 0.05]; // Most orders are delivered
    const random = Math.random();
    let sum = 0;
    for (let i = 0; i < weights.length; i++) {
      sum += weights[i];
      if (random <= sum) return statuses[i];
    }
    return 'delivered';
  };
  
  // Generate shipping addresses
  const addresses = [
    "123 Main St, New York, NY 10001",
    "456 Oak Ave, Los Angeles, CA 90210",
    "789 Pine Rd, Chicago, IL 60601",
    "321 Elm St, Houston, TX 77001",
    "654 Maple Dr, Phoenix, AZ 85001",
    "987 Cedar Ln, Philadelphia, PA 19101",
    "147 Birch Way, San Antonio, TX 78201",
    "258 Spruce St, San Diego, CA 92101",
    "369 Willow Ave, Dallas, TX 75201",
    "741 Poplar Rd, San Jose, CA 95101"
  ];
  
  // Generate payment methods
  const paymentMethods = [
    { type: 'credit_card', last4: '4242', brand: 'Visa' },
    { type: 'credit_card', last4: '5555', brand: 'Mastercard' },
    { type: 'credit_card', last4: '3782', brand: 'American Express' },
    { type: 'paypal', email: 'user@paypal.com' },
    { type: 'apple_pay', device: 'iPhone' },
    { type: 'google_pay', device: 'Android' }
  ];
  
  // Generate random order items with realistic quantities
  const generateOrderItems = (orderId) => {
    const numItems = Math.floor(Math.random() * 5) + 1; // 1-5 items per order
    const items = [];
    const usedProducts = new Set();
    
    for (let i = 0; i < numItems; i++) {
      let randomProduct;
      let attempts = 0;
      
      // Try to get a unique product, but allow duplicates if we can't find one
      do {
        randomProduct = products[Math.floor(Math.random() * products.length)];
        attempts++;
      } while (usedProducts.has(randomProduct.id) && attempts < 10);
      
      usedProducts.add(randomProduct.id);
      
      items.push({
        id: orderId * 100 + i,
        order: orderId,
        product: randomProduct.id,
        quantity: Math.floor(Math.random() * 3) + 1, // 1-3 quantity
        price_at_time: randomProduct.price // Store price at time of order
      });
    }
    
    return items;
  };
  
  // Calculate order totals
  const calculateOrderTotal = (items, products) => {
    let subtotal = 0;
    items.forEach(item => {
      const product = products.find(p => p.id === item.product);
      if (product) {
        subtotal += (item.price_at_time || product.price) * item.quantity;
      }
    });
    
    const tax = subtotal * 0.08; // 8% tax
    const shipping = subtotal > 1000 ? 0 : 50; // Free shipping over $1000
    const total = subtotal + tax + shipping;
    
    return { subtotal, tax, shipping, total };
  };
  
  // Generate orders
  const orders = [];
  for (let i = 1; i <= 25; i++) { // Generate 25 orders for better admin testing
    const customer = customers[Math.floor(Math.random() * customers.length)];
    const items = generateOrderItems(i);
    const totals = calculateOrderTotal(items, products);
    const status = getRandomStatus();
    const paymentMethod = paymentMethods[Math.floor(Math.random() * paymentMethods.length)];
    const shippingAddress = addresses[Math.floor(Math.random() * addresses.length)];
    
    orders.push({
      id: i,
      customer: customer.name,
      customer_email: customer.email,
      customer_phone: customer.phone,
      date_ordered: getRandomDate(),
      status: status,
      complete: status === 'delivered' || status === 'shipped',
      items: items,
      subtotal: totals.subtotal,
      tax: totals.tax,
      shipping: totals.shipping,
      total: totals.total,
      payment_method: paymentMethod,
      shipping_address: shippingAddress,
      tracking_number: status === 'shipped' || status === 'delivered' ? 
        `TRK${Math.random().toString(36).substr(2, 9).toUpperCase()}` : null,
      notes: i % 5 === 0 ? "Customer requested expedited shipping" : null
    });
  }
  
  // Sort orders by date (newest first)
  return orders.sort((a, b) => new Date(b.date_ordered) - new Date(a.date_ordered));
};

// Generate sales analytics data
const generateSalesAnalytics = (orders) => {
  const now = new Date();
  const thirtyDaysAgo = new Date(now.getTime() - 30 * 24 * 60 * 60 * 1000);
  const sevenDaysAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
  
  // Filter orders by time periods
  const last30Days = orders.filter(order => new Date(order.date_ordered) >= thirtyDaysAgo);
  const last7Days = orders.filter(order => new Date(order.date_ordered) >= sevenDaysAgo);
  const today = orders.filter(order => {
    const orderDate = new Date(order.date_ordered);
    return orderDate.toDateString() === now.toDateString();
  });
  
  // Calculate totals
  const calculateTotals = (orderList) => {
    return orderList.reduce((acc, order) => {
      acc.revenue += order.total;
      acc.orders += 1;
      acc.items += order.items.length;
      return acc;
    }, { revenue: 0, orders: 0, items: 0 });
  };
  
  return {
    today: calculateTotals(today),
    last7Days: calculateTotals(last7Days),
    last30Days: calculateTotals(last30Days),
    total: calculateTotals(orders)
  };
};

// Generate inventory alerts
const generateInventoryAlerts = (products) => {
  const alerts = [];
  
  products.forEach(product => {
    if (product.stock === 0) {
      alerts.push({
        id: `alert-${product.id}`,
        type: 'out_of_stock',
        product_id: product.id,
        product_name: product.name,
        message: `${product.name} is out of stock`,
        severity: 'high',
        created_at: new Date().toISOString()
      });
    } else if (product.stock < 5) {
      alerts.push({
        id: `alert-${product.id}`,
        type: 'low_stock',
        product_id: product.id,
        product_name: product.name,
        message: `${product.name} has low stock (${product.stock} remaining)`,
        severity: 'medium',
        created_at: new Date().toISOString()
      });
    }
  });
  
  return alerts;
};

// Initialize admin mock data
export const initializeAdminMockData = () => {
  if (!localStorage.getItem('adminMockProducts')) {
    const products = generateAdminMockProducts();
    localStorage.setItem('adminMockProducts', JSON.stringify(products));
  }
  
  if (!localStorage.getItem('adminMockOrders')) {
    const products = JSON.parse(localStorage.getItem('adminMockProducts'));
    const orders = generateAdminMockOrders(products);
    localStorage.setItem('adminMockOrders', JSON.stringify(orders));
  }
  
  // Generate analytics and alerts
  const products = JSON.parse(localStorage.getItem('adminMockProducts'));
  const orders = JSON.parse(localStorage.getItem('adminMockOrders'));
  const analytics = generateSalesAnalytics(orders);
  const alerts = generateInventoryAlerts(products);
  
  localStorage.setItem('adminAnalytics', JSON.stringify(analytics));
  localStorage.setItem('adminAlerts', JSON.stringify(alerts));
  
  return {
    products,
    orders,
    analytics,
    alerts
  };
};

// Get admin analytics
export const getAdminAnalytics = () => {
  return JSON.parse(localStorage.getItem('adminAnalytics')) || {};
};

// Get admin alerts
export const getAdminAlerts = () => {
  return JSON.parse(localStorage.getItem('adminAlerts')) || [];
};

// Update order status
export const updateOrderStatus = (orderId, newStatus) => {
  const orders = JSON.parse(localStorage.getItem('adminMockOrders')) || [];
  const orderIndex = orders.findIndex(order => order.id === orderId);
  
  if (orderIndex !== -1) {
    orders[orderIndex].status = newStatus;
    orders[orderIndex].complete = newStatus === 'delivered' || newStatus === 'shipped';
    
    // Add tracking number if shipped
    if (newStatus === 'shipped' && !orders[orderIndex].tracking_number) {
      orders[orderIndex].tracking_number = `TRK${Math.random().toString(36).substr(2, 9).toUpperCase()}`;
    }
    
    localStorage.setItem('adminMockOrders', JSON.stringify(orders));
    return orders[orderIndex];
  }
  
  return null;
};

export default {
  initializeAdminMockData,
  getAdminAnalytics,
  getAdminAlerts,
  updateOrderStatus
};