const generateMockOrders = (products) => {
  // Create some mock customers
  const customers = [
    { id: 1, name: "John Doe" },
    { id: 2, name: "Jane Smith" },
    { id: 3, name: "Robert Johnson" }
  ];
  
  // Generate random dates within the last 30 days
  const getRandomDate = () => {
    const now = new Date();
    const daysAgo = Math.floor(Math.random() * 30);
    now.setDate(now.getDate() - daysAgo);
    return now.toISOString();
  };
  
  // Generate random order items
  const generateOrderItems = (orderId) => {
    const numItems = Math.floor(Math.random() * 4) + 1; // 1-4 items per order
    const items = [];
    
    for (let i = 0; i < numItems; i++) {
      const randomProduct = products[Math.floor(Math.random() * products.length)];
      const existingItem = items.find(item => item.product === randomProduct.id);
      
      if (existingItem) {
        existingItem.quantity += Math.floor(Math.random() * 2) + 1;
      } else {
        items.push({
          id: orderId * 100 + i,
          order: orderId,
          product: randomProduct.id,
          quantity: Math.floor(Math.random() * 3) + 1 // 1-3 quantity
        });
      }
    }
    
    return items;
  };
  
  // Generate orders
  const orders = [];
  for (let i = 1; i <= 10; i++) {
    const customer = customers[Math.floor(Math.random() * customers.length)];
    
    orders.push({
      id: i,
      customer: customer.name,
      date_ordered: getRandomDate(),
      complete: true,
      items: generateOrderItems(i)
    });
  }
  
  return orders;
};

// Initialize mock data in localStorage if not already present
export const initializeMockData = () => {
  if (!localStorage.getItem('mockProducts')) {
    const products = generateMockProducts();
    localStorage.setItem('mockProducts', JSON.stringify(products));
  }
  
  if (!localStorage.getItem('mockOrders')) {
    const products = JSON.parse(localStorage.getItem('mockProducts'));
    const orders = generateMockOrders(products);
    localStorage.setItem('mockOrders', JSON.stringify(orders));
  }
  
  return {
    products: JSON.parse(localStorage.getItem('mockProducts')),
    orders: JSON.parse(localStorage.getItem('mockOrders'))
  };
};

// Get mock products
export const getMockProducts = () => {
  return JSON.parse(localStorage.getItem('mockProducts')) || [];
};

// Get mock orders
export const getMockOrders = () => {
  return JSON.parse(localStorage.getItem('mockOrders')) || [];
};

// Add a new product
export const addMockProduct = (product) => {
  const products = getMockProducts();
  const newProduct = {
    ...product,
    id: products.length > 0 ? Math.max(...products.map(p => p.id)) + 1 : 1
  };
  
  products.push(newProduct);
  localStorage.setItem('mockProducts', JSON.stringify(products));
  return newProduct;
};

// Update a product
export const updateMockProduct = (id, updates) => {
  const products = getMockProducts();
  const index = products.findIndex(p => p.id === id);
  
  if (index !== -1) {
    products[index] = { ...products[index], ...updates };
    localStorage.setItem('mockProducts', JSON.stringify(products));
    return products[index];
  }
  
  return null;
};

// Delete a product
export const deleteMockProduct = (id) => {
  const products = getMockProducts();
  const filteredProducts = products.filter(p => p.id !== id);
  
  if (filteredProducts.length < products.length) {
    localStorage.setItem('mockProducts', JSON.stringify(filteredProducts));
    return true;
  }
  
  return false;
};

// Add a new order
export const addMockOrder = (order) => {
  const orders = getMockOrders();
  const newOrder = {
    ...order,
    id: orders.length > 0 ? Math.max(...orders.map(o => o.id)) + 1 : 1,
    date_ordered: new Date().toISOString(),
    complete: true
  };
  
  orders.push(newOrder);
  localStorage.setItem('mockOrders', JSON.stringify(orders));
  
  // Update product stock
  if (newOrder.items && newOrder.items.length > 0) {
    const products = getMockProducts();
    
    newOrder.items.forEach(item => {
      const productIndex = products.findIndex(p => p.id === item.product);
      if (productIndex !== -1) {
        products[productIndex].stock = Math.max(0, products[productIndex].stock - item.quantity);
      }
    });
    
    localStorage.setItem('mockProducts', JSON.stringify(products));
  }
  
  return newOrder;
};

export default {
  initializeMockData,
  getMockProducts,
  getMockOrders,
  addMockProduct,
  updateMockProduct,
  deleteMockProduct,
  addMockOrder
};