import mockData from '../data/mockData';

// Initialize mock data
mockData.initializeMockData();

// Simulate API delay
const delay = (ms = 300) => new Promise(resolve => setTimeout(resolve, ms));

const mockApiService = {
  // Products API
  getProducts: async () => {
    await delay();
    return { data: mockData.getMockProducts() };
  },
  
  getProduct: async (id) => {
    await delay();
    const products = mockData.getMockProducts();
    const product = products.find(p => p.id === id);
    
    if (!product) {
      return Promise.reject({ response: { status: 404, data: { error: 'Product not found' } } });
    }
    
    return { data: product };
  },
  
  createProduct: async (productData) => {
    await delay(500);
    const newProduct = mockData.addMockProduct(productData);
    return { data: newProduct };
  },
  
  updateProduct: async (id, updates) => {
    await delay(500);
    const updatedProduct = mockData.updateMockProduct(id, updates);
    
    if (!updatedProduct) {
      return Promise.reject({ response: { status: 404, data: { error: 'Product not found' } } });
    }
    
    return { data: updatedProduct };
  },
  
  deleteProduct: async (id) => {
    await delay(500);
    const success = mockData.deleteMockProduct(id);
    
    if (!success) {
      return Promise.reject({ response: { status: 404, data: { error: 'Product not found' } } });
    }
    
    return { data: { success: true } };
  },
  
  // Orders API
  getOrders: async () => {
    await delay();
    return { data: mockData.getMockOrders() };
  },
  
  createOrder: async (orderData) => {
    await delay(800);
    const newOrder = mockData.addMockOrder(orderData);
    return { data: newOrder };
  },
  
  // Authentication API
  login: async (credentials) => {
    await delay(1000);
    
    // Demo login logic
    if (credentials.username === 'admin' && credentials.password === 'admin') {
      return {
        data: {
          access: 'mock-token-admin',
          refresh: 'mock-refresh-token',
          user: {
            id: 1,
            username: 'admin',
            is_staff: true
          }
        }
      };
    } else if (credentials.username === 'user' && credentials.password === 'user') {
      return {
        data: {
          access: 'mock-token-user',
          refresh: 'mock-refresh-token',
          user: {
            id: 2,
            username: 'user',
            is_staff: false
          }
        }
      };
    }
    
    // Invalid credentials
    return Promise.reject({
      response: {
        status: 401,
        data: { detail: 'Invalid username or password' }
      }
    });
  },
  
  register: async (userData) => {
    await delay(1000);
    
    // Check if username already exists
    if (userData.username === 'admin' || userData.username === 'user') {
      return Promise.reject({
        response: {
          status: 400,
          data: { username: ['This username is already taken.'] }
        }
      });
    }
    
    return {
      data: {
        id: 3,
        username: userData.username,
        is_staff: false
      }
    };
  },
  
  getUser: async () => {
    await delay();
    
    const isStaff = localStorage.getItem('is_staff') === 'true';
    const userId = localStorage.getItem('user_id') || (isStaff ? '1' : '2');
    const username = localStorage.getItem('username') || (isStaff ? 'admin' : 'user');
    
    return {
      data: {
        id: parseInt(userId),
        username: username,
        is_staff: isStaff
      }
    };
  },
  
  // Generic request handler with method support
  request: async (config) => {
    const { method = 'get', url, data } = config;
    
    // Extract the endpoint and ID from the URL
    const parts = url.split('/').filter(Boolean);
    const endpoint = parts[0];
    const id = parts[1] ? parseInt(parts[1]) : null;
    
    switch (endpoint) {
      case 'products':
        if (method === 'get') {
          return id ? mockApiService.getProduct(id) : mockApiService.getProducts();
        } else if (method === 'post') {
          return mockApiService.createProduct(data);
        } else if (method === 'put' || method === 'patch') {
          return mockApiService.updateProduct(id, data);
        } else if (method === 'delete') {
          return mockApiService.deleteProduct(id);
        }
        break;
        
      case 'orders':
        if (method === 'get') {
          return mockApiService.getOrders();
        } else if (method === 'post') {
          return mockApiService.createOrder(data);
        }
        break;
        
      case 'token':
        if (method === 'post') {
          return mockApiService.login(data);
        }
        break;
        
      case 'register':
        if (method === 'post') {
          return mockApiService.register(data);
        }
        break;
        
      case 'user':
        if (method === 'get') {
          return mockApiService.getUser();
        }
        break;
        
      case 'order-items':
        if (method === 'post') {
          // Just simulate success for order items
          await delay(300);
          return { data: { ...data, id: Math.floor(Math.random() * 1000) } };
        }
        break;
    }
    
    return Promise.reject({
      response: {
        status: 404,
        data: { error: 'Endpoint not found' }
      }
    });
  }
};

// Create a proxy to handle all axios-like methods
const apiProxy = new Proxy(mockApiService, {
  get: (target, prop) => {
    if (['get', 'post', 'put', 'patch', 'delete'].includes(prop)) {
      return (url, data, config = {}) => {
        return target.request({
          method: prop,
          url,
          data,
          ...config
        });
      };
    }
    return target[prop];
  }
});

export default apiProxy;