import adminMockData from '../data/adminMockData';

// Initialize admin mock data
adminMockData.initializeAdminMockData();

// Simulate API delay
const delay = (ms = 300) => new Promise(resolve => setTimeout(resolve, ms));

const adminMockApiService = {
  // Enhanced Products API for Admin
  getProducts: async () => {
    await delay();
    return { data: JSON.parse(localStorage.getItem('adminMockProducts')) || [] };
  },
  
  getProduct: async (id) => {
    await delay();
    const products = JSON.parse(localStorage.getItem('adminMockProducts')) || [];
    const product = products.find(p => p.id === parseInt(id));
    
    if (!product) {
      return Promise.reject({ response: { status: 404, data: { error: 'Product not found' } } });
    }
    
    return { data: product };
  },
  
  createProduct: async (productData) => {
    await delay(800);
    const products = JSON.parse(localStorage.getItem('adminMockProducts')) || [];
    
    const newProduct = {
      ...productData,
      id: products.length > 0 ? Math.max(...products.map(p => p.id)) + 1 : 1,
      sku: `PRD-${String(products.length + 1).padStart(3, '0')}`,
      created_at: new Date().toISOString(),
      updated_at: new Date().toISOString(),
      category: productData.category || 'General'
    };
    
    products.push(newProduct);
    localStorage.setItem('adminMockProducts', JSON.stringify(products));
    
    // Update regular products for customer view
    const customerProducts = JSON.parse(localStorage.getItem('mockProducts')) || [];
    customerProducts.push(newProduct);
    localStorage.setItem('mockProducts', JSON.stringify(customerProducts));
    
    return { data: newProduct };
  },
  
  updateProduct: async (id, updates) => {
    await delay(600);
    const products = JSON.parse(localStorage.getItem('adminMockProducts')) || [];
    const index = products.findIndex(p => p.id === parseInt(id));
    
    if (index === -1) {
      return Promise.reject({ response: { status: 404, data: { error: 'Product not found' } } });
    }
    
    products[index] = { 
      ...products[index], 
      ...updates, 
      updated_at: new Date().toISOString() 
    };
    
    localStorage.setItem('adminMockProducts', JSON.stringify(products));
    
    // Update customer products too
    const customerProducts = JSON.parse(localStorage.getItem('mockProducts')) || [];
    const customerIndex = customerProducts.findIndex(p => p.id === parseInt(id));
    if (customerIndex !== -1) {
      customerProducts[customerIndex] = products[index];
      localStorage.setItem('mockProducts', JSON.stringify(customerProducts));
    }
    
    return { data: products[index] };
  },
  
  deleteProduct: async (id) => {
    await delay(500);
    const products = JSON.parse(localStorage.getItem('adminMockProducts')) || [];
    const orders = JSON.parse(localStorage.getItem('adminMockOrders')) || [];
    
    // Check if product is in any orders
    const productInOrders = orders.some(order => 
      order.items.some(item => item.product === parseInt(id))
    );
    
    if (productInOrders) {
      return Promise.reject({ 
        response: { 
          status: 400, 
          data: { error: 'Cannot delete product. It exists in one or more orders.' } 
        } 
      });
    }
    
    const filteredProducts = products.filter(p => p.id !== parseInt(id));
    
    if (filteredProducts.length === products.length) {
      return Promise.reject({ response: { status: 404, data: { error: 'Product not found' } } });
    }
    
    localStorage.setItem('adminMockProducts', JSON.stringify(filteredProducts));
    
    // Update customer products too
    const customerProducts = JSON.parse(localStorage.getItem('mockProducts')) || [];
    const filteredCustomerProducts = customerProducts.filter(p => p.id !== parseInt(id));
    localStorage.setItem('mockProducts', JSON.stringify(filteredCustomerProducts));
    
    return { data: { success: true } };
  },
  
  // Enhanced Orders API for Admin
  getOrders: async () => {
    await delay();
    return { data: JSON.parse(localStorage.getItem('adminMockOrders')) || [] };
  },
  
  getOrder: async (id) => {
    await delay();
    const orders = JSON.parse(localStorage.getItem('adminMockOrders')) || [];
    const order = orders.find(o => o.id === parseInt(id));
    
    if (!order) {
      return Promise.reject({ response: { status: 404, data: { error: 'Order not found' } } });
    }
    
    return { data: order };
  },
  
  updateOrderStatus: async (id, statusData) => {
    await delay(400);
    const updatedOrder = adminMockData.updateOrderStatus(parseInt(id), statusData.status);
    
    if (!updatedOrder) {
      return Promise.reject({ response: { status: 404, data: { error: 'Order not found' } } });
    }
    
    return { data: updatedOrder };
  },
  
  createOrder: async (orderData) => {
    await delay(800);
    const orders = JSON.parse(localStorage.getItem('adminMockOrders')) || [];
    const products = JSON.parse(localStorage.getItem('adminMockProducts')) || [];
    
    const newOrder = {
      ...orderData,
      id: orders.length > 0 ? Math.max(...orders.map(o => o.id)) + 1 : 1,
      date_ordered: new Date().toISOString(),
      status: 'pending',
      complete: false
    };
    
    orders.push(newOrder);
    localStorage.setItem('adminMockOrders', JSON.stringify(orders));
    
    // Update customer orders too
    const customerOrders = JSON.parse(localStorage.getItem('mockOrders')) || [];
    customerOrders.push(newOrder);
    localStorage.setItem('mockOrders', JSON.stringify(customerOrders));
    
    // Update product stock if order has items
    if (newOrder.items && newOrder.items.length > 0) {
      newOrder.items.forEach(item => {
        const productIndex = products.findIndex(p => p.id === item.product);
        if (productIndex !== -1) {
          products[productIndex].stock = Math.max(0, products[productIndex].stock - item.quantity);
        }
      });
      
      localStorage.setItem('adminMockProducts', JSON.stringify(products));
      
      // Update customer products too
      const customerProducts = JSON.parse(localStorage.getItem('mockProducts')) || [];
      newOrder.items.forEach(item => {
        const customerProductIndex = customerProducts.findIndex(p => p.id === item.product);
        if (customerProductIndex !== -1) {
          customerProducts[customerProductIndex].stock = Math.max(0, customerProducts[customerProductIndex].stock - item.quantity);
        }
      });
      localStorage.setItem('mockProducts', JSON.stringify(customerProducts));
    }
    
    return { data: newOrder };
  },
  
  // Analytics API
  getAnalytics: async () => {
    await delay();
    return { data: adminMockData.getAdminAnalytics() };
  },
  
  // Alerts API
  getAlerts: async () => {
    await delay();
    return { data: adminMockData.getAdminAlerts() };
  },
  
  // Dashboard stats
  getDashboardStats: async () => {
    await delay();
    const products = JSON.parse(localStorage.getItem('adminMockProducts')) || [];
    const orders = JSON.parse(localStorage.getItem('adminMockOrders')) || [];
    const analytics = adminMockData.getAdminAnalytics();
    
    const lowStockCount = products.filter(p => p.stock < 5).length;
    const outOfStockCount = products.filter(p => p.stock === 0).length;
    const pendingOrders = orders.filter(o => o.status === 'pending').length;
    const totalRevenue = orders.reduce((sum, order) => sum + order.total, 0);
    
    return {
      data: {
        totalProducts: products.length,
        totalOrders: orders.length,
        totalRevenue: totalRevenue,
        lowStockCount: lowStockCount,
        outOfStockCount: outOfStockCount,
        pendingOrders: pendingOrders,
        analytics: analytics
      }
    };
  },
  
  // User management (basic)
  getUser: async () => {
    await delay();
    
    const isStaff = localStorage.getItem('is_staff') === 'true';
    const userId = localStorage.getItem('user_id') || (isStaff ? '1' : '2');
    const username = localStorage.getItem('username') || (isStaff ? 'admin' : 'user');
    
    return {
      data: {
        id: parseInt(userId),
        username: username,
        is_staff: isStaff,
        email: isStaff ? 'admin@bluora.com' : 'user@bluora.com',
        first_name: isStaff ? 'Admin' : 'User',
        last_name: isStaff ? 'User' : 'Customer',
        date_joined: '2024-01-01T00:00:00Z'
      }
    };
  },
  
  // Authentication
  login: async (credentials) => {
    await delay(1000);
    
    // Demo login logic
    if (credentials.username === 'admin' && credentials.password === 'admin') {
      return {
        data: {
          access: 'mock-token-admin',
          refresh: 'mock-refresh-token'
        }
      };
    } else if (credentials.username === 'user' && credentials.password === 'user') {
      return {
        data: {
          access: 'mock-token-user',
          refresh: 'mock-refresh-token'
        }
      };
    }
    
    return Promise.reject({
      response: {
        status: 401,
        data: { detail: 'Invalid username or password' }
      }
    });
  },
  
  // Generic request handler
  request: async (config) => {
    const { method = 'get', url, data } = config;
    
    // Extract the endpoint and ID from the URL
    const parts = url.split('/').filter(Boolean);
    const endpoint = parts[0];
    const id = parts[1] ? parseInt(parts[1]) : null;
    
    switch (endpoint) {
      case 'products':
        if (method === 'get') {
          return id ? adminMockApiService.getProduct(id) : adminMockApiService.getProducts();
        } else if (method === 'post') {
          return adminMockApiService.createProduct(data);
        } else if (method === 'put' || method === 'patch') {
          return adminMockApiService.updateProduct(id, data);
        } else if (method === 'delete') {
          return adminMockApiService.deleteProduct(id);
        }
        break;
        
      case 'orders':
        if (method === 'get') {
          return id ? adminMockApiService.getOrder(id) : adminMockApiService.getOrders();
        } else if (method === 'post') {
          return adminMockApiService.createOrder(data);
        } else if (method === 'patch') {
          return adminMockApiService.updateOrderStatus(id, data);
        }
        break;
        
      case 'analytics':
        if (method === 'get') {
          return adminMockApiService.getAnalytics();
        }
        break;
        
      case 'alerts':
        if (method === 'get') {
          return adminMockApiService.getAlerts();
        }
        break;
        
      case 'dashboard':
        if (method === 'get') {
          return adminMockApiService.getDashboardStats();
        }
        break;
        
      case 'token':
        if (method === 'post') {
          return adminMockApiService.login(data);
        }
        break;
        
      case 'user':
        if (method === 'get') {
          return adminMockApiService.getUser();
        }
        break;
        
      case 'order-items':
        if (method === 'post') {
          await delay(300);
          return { data: { ...data, id: Math.floor(Math.random() * 1000) } };
        }
        break;
    }
    
    return Promise.reject({
      response: {
        status: 404,
        data: { error: 'Endpoint not found' }
      }
    });
  }
};

// Create a proxy to handle all axios-like methods
const adminApiProxy = new Proxy(adminMockApiService, {
  get: (target, prop) => {
    if (['get', 'post', 'put', 'patch', 'delete'].includes(prop)) {
      return (url, data, config = {}) => {
        return target.request({
          method: prop,
          url,
          data,
          ...config
        });
      };
    }
    return target[prop];
  }
});

export default adminApiProxy;