import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import { auth } from './auth' 
import 'bootstrap/dist/css/bootstrap.min.css'

// Import mock data initializers for demo mode
import { initializeMockData } from './data/mockData'
import { initializeAdminMockData } from './data/adminMockData'

// Check if we're in demo mode
const isDemoMode = window.location.hostname.includes('github.io') || 
                   process.env.VUE_APP_DEMO_MODE === 'true';

// Initialize mock data if in demo mode
if (isDemoMode) {
  console.log('🚀 Running in demo mode with comprehensive mock data')
  
  // Initialize both customer and admin mock data
  initializeMockData()
  initializeAdminMockData()
  
  console.log('📊 Mock data initialized:')
  console.log('- Products:', JSON.parse(localStorage.getItem('adminMockProducts')).length)
  console.log('- Orders:', JSON.parse(localStorage.getItem('adminMockOrders')).length)
  console.log('- Demo Credentials:')
  console.log('  Admin: username="admin", password="admin"')
  console.log('  Customer: username="user", password="user"')
}

const app = createApp(App)
app.use(router)
app.provide('auth', auth) 
app.mount('#app')