import { reactive } from 'vue'
import axios from './axios'

const auth = reactive({
  isLoggedIn: false,
  isStaff: false,

  init() {
    this.isLoggedIn = !!localStorage.getItem('access')
    this.isStaff = localStorage.getItem('is_staff') === 'true'
  },

  async checkTokenValidity() {
    // Skip check in demo mode
    if (process.env.VUE_APP_DEMO_MODE === 'true') return true

    const token = localStorage.getItem('access')
    if (!token) return false

    try {
      await axios.get('user/')
      return true
    } catch (error) {
      console.log('Token invalid or expired, logging out')
      this.logout()
      return false
    }
  },

  logout(redirectToGithubPages = false) {
    const cart = localStorage.getItem('cart')
    localStorage.clear()
    if (cart) localStorage.setItem('cart', cart)

    this.isLoggedIn = false
    this.isStaff = false

    if (redirectToGithubPages) {
      // This will be handled by the component using router
    }
  }
})

// Initialize immediately
auth.init()

export { auth }
